:root {
  --primary: #fff;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
  &--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
  }
  &--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }
  &--medium {
    padding: 8px 20px;
    font-size: 18px;
    &:hover {
      transition: all 0.3s ease-out;
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }
  &--large {
    padding: 12px 26px;
    font-size: 20px;
    &:hover {
    }
  }
}
