.footer-container {
  background-color: #242424;
  padding: 4rem 0 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 24px;
  padding: 24px;
  color: #fff; }
  .subscription__heading {
    margin-bottom: 24px;
    font-size: 24px; }
  .subscription__text {
    margin-bottom: 24px;
    font-size: 20px; }
  .subscription__input {
    padding: 8px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #fff; }
  .subscription > p {
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif; }

.links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center; }
  .links__wrapper {
    display: flex; }
  .links__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box; }
    .links__item > h2 {
      color: #fff; }
    .links__item h2 {
      margin-bottom: 16px; }
    .links__item a {
      color: #fff;
      text-decoration: none;
      margin-bottom: 0.5rem; }
      .links__item a:hover {
        color: #e9e9e9;
        transition: 0.3s ease-out; }

.fas {
  margin-left: 15px; }

/* Social Icons */
.social-icon-link {
  color: #fff;
  font-size: 24px; }

.socialmedia {
  max-width: 1000px;
  width: 100%; }
  .socialmedia__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: 40px auto 0 auto; }

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px; }

.social-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px; }

.website-rights {
  color: #fff;
  margin-bottom: 16px; }

@media screen and (max-width: 820px) {
  .links {
    padding-top: 2rem; }
  .footer-input {
    width: 100%; }
  .btn {
    width: 100%; }
  .links__wrapper {
    flex-direction: column; }
  .socialmedia__wrap {
    flex-direction: column; } }
