.navbar {
  background: rgb(195, 171, 34);
  background: linear-gradient(
    0deg,
    rgba(195, 171, 34, 1) 14%,
    rgba(207, 202, 38, 1) 60%,
    rgba(212, 213, 40, 1) 73%,
    rgba(217, 226, 42, 1) 83%,
    rgba(228, 253, 45, 0.9139005944174545) 100%
  );
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100%;
  &.active {
    background-color: #fbb034;
    background-image: linear-gradient(315deg, #fbb034 0%, #ffdd00 74%);
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: fixed;
    top: 0;
    z-index: 999;
    width: 100%;
  }
  &__logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  &__menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 80vw;
    justify-content: end;
    margin-right: 2rem;
  }
  &__item {
    height: 60px;
  }
  &__links {
    color: rgb(59, 55, 55);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    font-weight: bold;
    &:hover {
      border-bottom: 4px solid rgb(73, 69, 69);
      transition: all 0.2s ease-out;
    }
    &.active {
      color: yellow;
      border-bottom: 4px solid rgb(73, 69, 69);
    }
  }
  &__picture {
    width: 100px;
    height: 100px;
    display: block;
    margin-left: 30px;
    color: #fff;
    top: 1px;
  }
  &__basket {
    position: relative;
    margin-right: 50px;
    & span {
      background: crimson;
      border-radius: 20px;
      color: White;
      position: absolute;
      top: -10px;
      right: -10px;
      font-size: 10px;
      padding: 5px;
    }
  }
  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  &__icon {
    display: none;
  }
}

.fa-bars {
  color: #fff;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }
  .navbar {
    &__menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 100px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      &.active {
        background: #242222;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
      }
    }
    &__links {
      text-align: center;
      padding: 1rem;
      width: 100%;
      display: table;
      color: white;
      transition: 250ms;
      &:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 0;
      }
    }
    &__logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
    &__picture {
      position: fixed;
    }
    &__basket {
      position: absolute;
      right: 50px;
      top: 30px;
    }
    &__icon {
      display: block;
      position: absolute;
      top: 12px;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  }
  .fa-times {
    color: #fff;
    font-size: 2rem;
  }
}
